import { useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
const Promjenaifre = ({ setPromjenaifre }) => {
  const [staraifra, setStaraifra] = useState("");
  const [novaifra, setNovaifra] = useState("");
  const [potvrdaNoveifre, setPotvrdaNoveifre] = useState("");

  const navigate = useNavigate();

  const promjenaifreCB = () => {
    setPromjenaifre(false);
  };

  const postaviStaruifru = (e) => {
    if (e.target) setStaraifra(e.target.value);
  };
  const postaviNovuifru = (e) => {
    if (e.target) setNovaifra(e.target.value);
  };
  const postaviPotvrduNoveifre = (e) => {
    if (e.target) setPotvrdaNoveifre(e.target.value);
  };

  const sauvajPodatkeCB = async () => {
    if (novaifra !== potvrdaNoveifre) {
      alert("ifre se moraju poklapati!");
      return;
    }
    if (staraifra === novaifra) {
      alert("Nova ifra je ista kao stara!");
      return;
    }
    const podaci = {
      ifra: staraifra,
      novaifra: novaifra,
    };

    const url = await fetch("https://e-notes-4mhk.onrender.com/promjenaSifre", {
      method: "PATCH",
      credentials: "include",
      redirect: "follow",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(podaci),
    });

    const res = await url.json();
    alert("Uspjeno aurirani podaci :D");
    navigate("/");
  };

  return (
    <span className="grid">
      <span>
        <div className="flex flex-col gap-4 rounded-3xl mt-8">
          {/* STARA IFRA*/}
          <div className="pl-4 flex items-center gap-4 relative w-full h-20 border-b-2 border-white">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              stroke-width="1.5"
              stroke="white"
              class="w-12 h-12 flex align-middle justify-center hover:rotate-6 animacija cursor-pointer"
            >
              <path
                stroke-linecap="round"
                stroke-linejoin="round"
                d="M16.862 4.487l1.687-1.688a1.875 1.875 0 112.652 2.652L10.582 16.07a4.5 4.5 0 01-1.897 1.13L6 18l.8-2.685a4.5 4.5 0 011.13-1.897l8.932-8.931zm0 0L19.5 7.125M18 14v4.75A2.25 2.25 0 0115.75 21H5.25A2.25 2.25 0 013 18.75V8.25A2.25 2.25 0 015.25 6H10"
              />
            </svg>
            <input
              className="h-full text-white text-2xl font-semibold focus:border-0 bg-transparent border-none outline-none placeholder:text-white"
              placeholder="Unesi staru ifru"
              value={staraifra}
              onChange={postaviStaruifru.bind(Event)}
              onKeyDown={postaviStaruifru.bind(Event)}
              type="password"
            ></input>
          </div>

          {/* NOVA IFRA */}
          <div className="pl-4 flex items-center gap-4 relative w-full h-20 border-b-2 border-white">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              stroke-width="1.5"
              stroke="white"
              class="w-12 h-12 flex align-middle justify-center hover:rotate-6 animacija cursor-pointer"
            >
              <path
                stroke-linecap="round"
                stroke-linejoin="round"
                d="M16.862 4.487l1.687-1.688a1.875 1.875 0 112.652 2.652L10.582 16.07a4.5 4.5 0 01-1.897 1.13L6 18l.8-2.685a4.5 4.5 0 011.13-1.897l8.932-8.931zm0 0L19.5 7.125M18 14v4.75A2.25 2.25 0 0115.75 21H5.25A2.25 2.25 0 013 18.75V8.25A2.25 2.25 0 015.25 6H10"
              />
            </svg>
            <input
              className="h-full text-white text-2xl font-semibold focus:border-0 bg-transparent border-none outline-none placeholder:text-white"
              placeholder="Unesi novu ifru"
              value={novaifra}
              onChange={postaviNovuifru.bind(Event)}
              onKeyDown={postaviNovuifru.bind(Event)}
              type="password"
            ></input>
          </div>

          {/* POTVRDA NOVE IFRE*/}
          <div className="pl-4 flex items-center gap-4 relative w-full h-20 border-b-2 border-white">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              stroke-width="1.5"
              stroke="white"
              class="w-12 h-12 flex align-middle justify-center hover:rotate-6 animacija cursor-pointer"
            >
              <path
                stroke-linecap="round"
                stroke-linejoin="round"
                d="M16.862 4.487l1.687-1.688a1.875 1.875 0 112.652 2.652L10.582 16.07a4.5 4.5 0 01-1.897 1.13L6 18l.8-2.685a4.5 4.5 0 011.13-1.897l8.932-8.931zm0 0L19.5 7.125M18 14v4.75A2.25 2.25 0 0115.75 21H5.25A2.25 2.25 0 013 18.75V8.25A2.25 2.25 0 015.25 6H10"
              />
            </svg>
            <input
              className="h-full text-white text-2xl font-semibold focus:border-0 bg-transparent border-none outline-none placeholder:text-white"
              placeholder="Ponovi novu ifru"
              value={potvrdaNoveifre}
              onChange={postaviPotvrduNoveifre.bind(Event)}
              onKeyDown={postaviPotvrduNoveifre.bind(Event)}
              type="password"
            ></input>
          </div>

          <span className="flex flex-row gap-4 rounded-3xl mt-8">
            <button
              className="bg-siva w-[40%] h-20 rounded-3xl font-bold text-2xl centriraj"
              onClick={promjenaifreCB}
            >
              Vrati se
            </button>
            <button
              className="bg-tamno-zelena w-[60%] h-20 rounded-3xl font-bold text-2xl text-siva centriraj"
              onClick={sauvajPodatkeCB}
            >
              Sauvaj
            </button>
          </span>
        </div>
      </span>
    </span>
  );
};

export default Promjenaifre;
