const sistemPretrage = (pretraga, naslovBiljeke) => {
  // trening -> Trening
  // 1. Mala velika slova
  pretraga = pretraga.toLowerCase();
  naslovBiljeke = naslovBiljeke.toLowerCase();
  if (naslovBiljeke.startsWith(pretraga)) {
    return true;
  }

  // 2. Razliite rijei
  const rijei = naslovBiljeke
    .split(" ")
    .filter((item) => item.startsWith(pretraga));
  if (rijei.length !== 0) return true;

  // 3. Spojen naslov
  naslovBiljeke = naslovBiljeke.split(" ").join("");
  if (naslovBiljeke.startsWith(pretraga)) {
    return true;
  }
  // Ako je sve netano, vrati false
  return false;
};

module.exports = sistemPretrage;
